<template>
  <div class="pc-max-width margin-t-80 enrollment clearfix">
    <h3></h3>
    <div class="input-box">
      <input type="text" v-model="name" placeholder="" />
      <p v-if="nameError"></p>
    </div>
    <div class="input-box">
      <input type="text" v-model="phone" placeholder="" />
      <p v-if="phoneError"></p>
    </div>
    <div class="no-m input-box">
      <input type="text" v-model="mailbox" placeholder="" />
      <p v-if="mailboxError"></p>
    </div>
    <div class="input-box">
      <select v-model="age">
        <option value=""></option>
        <option value="18">18</option>
        <option value="18">18</option>
      </select>
      <p v-if="ageError"></p>
    </div>
    <div class="input-box">
      <select v-model="englishLevel">
        <option value=""></option>
        <option value="PETS1">PETS1</option>
        <option value="PETS2">PETS2</option>
        <option value="PETS3">PETS3</option>
        <option value="PETS4">PETS4</option>
        <option value="PETS5">PETS5</option>
        <option value="PETS5"></option>
        <option value="PETS5"></option>
      </select>
      <p v-if="englishLevelError"></p>
    </div>
    <div class="no-m input-box">
      <select v-model="education">
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <p v-if="educationError"></p>
    </div>
    <button @click="submitFrom" :class="{load: submitLoad}">
      <template v-if="!submitLoad"></template>
      <img v-else src="../assets/images/message-loading.png" />
    </button>

    <MessagePopup v-if="messageType" :status="messageType" @messageType="messageType = false"></MessagePopup>
  </div>
</template>
<script>
import MessagePopup from './commonComponent/MessagePopup'
export default {
  components: {
    MessagePopup
  },
  data () {
    return {
      name: '',
      phone: '',
      mailbox: '',
      age: '',
      englishLevel: '',
      education: '',
      nameError: false,
      phoneError: false,
      mailboxError: false,
      ageError: false,
      englishLevelError: false,
      educationError: false,
      submitLoad: false,
      messageType: false
    }
  },
  methods: {
    submitFrom () {
      setTimeout(() => {
        this.nameError = false
        this.phoneError = false
        this.mailboxError = false
        this.ageError = false
        this.englishLevelError = false
        this.educationError = false
      }, 2000)
      if (!this.name) {
        this.nameError = true
        return false
      }
      if (!this.phone) {
        this.phoneError = true
        return false
      }
      if (!this.mailbox) {
        this.mailboxError = true
        return false
      }
      if (this.age === '') {
        this.ageError = true
        return false
      }
      if (this.englishLevel === '') {
        this.englishLevelError = true
        return false
      }
      if (this.education === '') {
        this.educationError = true
        return false
      }
      let params = {
        name: this.name,
        phone: this.phone,
        email: this.mailbox,
        age: this.age,
        english_level: this.englishLevel,
        education: this.education
      }
      if (this.submitLoad) return
      this.submitLoad = true
       this.$http({
            method: "post",
            url: process.env.VUE_APP_API + "/v1/contact",
            params
          }).then(res => {
            if (res.data.code === 200) {
              this.submitLoad = false
              this.messageType = true
              this.name = ''
              this.phone = ''
              this.mailbox = ''
              this.age = ''
              this.englishLevel = ''
              this.education = ''
            }
          })
    }
  }
}
</script>
